import React from 'react';
import ReactDOM from 'react-dom';
import * as utils from './utils.js';


export default class Card extends React.Component {

    constructor(props) {
        super(props);
        let values = props.value;
        let color = '#000';
        if(values.color === 'R'){
            color = '#FC0D1B';
        }
        let suite_uni = '';
        if(values.suite === '')
        this.state = {
            value: values.value,
            suite: suite_uni,
            text: values.text,
            color: color
        }
    }

    render() {
        return (
            <div className="card-container" style={{'color': this.state.color}}>
                <p>{this.state.text}</p>
            </div>
        )
    }


}


